<template>
  <div>
    <page-header title="QR Code 維護" :breadcrumb="breadcrumb" />
    <div class="right_top right_in">
      <div class="row">
        <div class="col-sm-12 col-md-6">
          <div class="d-flex select_row">
            <div class="tit">狀態</div>
            <div class="select_box w-100">
              <div class="extend-input">
                <input-select
                  v-model="formData.status"
                  track-by="value"
                  label="name"
                  :options="statusOptions"
                />
              </div>
            </div>
          </div>
        </div>

        <div class="col-sm-12 col-md-6">
          <div class="d-flex select_row">
            <div class="tit">尺寸(px)</div>
            <div class="select_box w-100 d-flex">
              <input
                id="inputSize"
                type="text"
                class="form-control"
                v-model="formData.size"
              />
            </div>
          </div>
        </div>

        <div class="col-sm-12 col-md-6">
          <div class="d-flex select_row">
            <div class="tit">URL</div>
            <div class="select_box w-100">
              <input type="text" class="form-control" v-model="formData.url" />
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col d-flex justify-content-center">
          <img
            v-if="formData.url && formData.size >= 100"
            :src="qrCodeUrl"
            alt="QR Code"
          />
        </div>
      </div>

      <div class="row">
        <div class="text-center">
          <button class="btn btn-blue" type="button">
            儲存
          </button>
          <button class="btn btn-blue" type="button">
            取消
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import InputSelect from '@/components/InputSelect'

export default {
  name: 'qrCode',
  data() {
    return {
      breadcrumb: [
        {
          title: 'QR Code 維護'
        }
      ],
      formData: this.getDefaultData(),
      statusOptions: [
        {
          name: '開啟',
          value: 'open'
        },
        {
          name: '關閉',
          value: 'close'
        }
      ]
    }
  },
  computed: {
    qrCodeUrl() {
      return (
        'https://chart.googleapis.com/chart?cht=qr&chl=' +
        this.formData.url +
        '&chs=' +
        this.formData.size
      )
    }
  },
  components: {
    InputSelect,
    PageHeader
  },
  methods: {
    getDefaultData() {
      return {
        status: 'open',
        url: '',
        width: 100,
        height: 100,
        size: 150
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.label-title {
  font-size: 16px;
  padding-top: 9px;
}
</style>
